* {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  margin: 0;
}

a, a:visited {
  color: #03a9f4;
}

header {
  position: fixed;
  padding: 10px 20px;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 15px;
  border-bottom: 1px solid #eee;
}

header a {
  margin-right: 10px;
  text-decoration: none;
}

header h1 {
  font-size: 18px;
  margin: 0 0 0 5px;
}

header .logo {
  display: flex;
}

main {
  margin: 40px 0;
  display: flex;
  flex-direction: column;
}

main .logo {
  display: flex;
  justify-content: center;
  margin: 80px auto 0;
  font-size: 32px;
}

main .logo h1 {
  margin: 0 10px;
}

h1.tagline {
  font-size: 36px;
  text-align: center;
  margin: 80px 0 100px;
}


img.screenshot {
  width: 80%;
  margin: 0 auto;
}

section {
  width: 800px;
  margin: 0 auto;


}

.features {
  margin-top: 100px;
}

.feature {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}

.feature img {
  width: 100%;
  border: 1px solid #eee;
}

.subscription {
  margin: 70px 0 70px;
}

@media (max-width: 800px) {
  section {
    width: 100%;
  }
}
